<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 v-if="AddQuestion" class="modal-title">{{ $t('AddQuestion') }}</h4>
                    <h4 v-if="EditQuestion" class="modal-title">{{ $t('EditQuestion') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                    <!-- <template v-slot:tgslot-CompensationBenefitInformation="{ data }">
                        <div class="row" v-for="(item, index) in StockData">
