                                        <input type="number" class="form-control" v-model="item.quantity">
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-2 d-flex align-items-center">
                                    <div class="form-group mb-0 mt-2">
                                        <a v-if="index == 0" v-on:click="AddRow"
                                            class="theme-primary round-icon-small btn-success-light"><em
                                                class="fa fa-plus"></em></a>
                                        <a v-if="index > 0" v-on:click="DeleteRow(item, index)" href="javascript:;"
                                            class="btn-danger-light theme-primary round-icon-small"><em
                                                class="fa fa-trash"></em></a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </template> -->
                    <div class="left-content row flex-row-reverse mt-3">
                        <div class="col-lg-12 text-left">
                            <div class="py-2 mandatory">
                                {{ $t('MandatoryString') }}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

    
<script>
import DataService from '../../../services/DataService'
export default {
    props: {
        AddQuestionId: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            //StockData: [{ stock: "", quantity: "" }],
            AddQuestion: true,
            EditQuestion: false,
            FormSchema: [{
                    layoutType: "four",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('FeedbackName'),
                        name: "FeedbackName",
                        value: "",
                        placeholder: this.$t('Enter Feedback Name'),
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    }, 
                    {
                        astype: "MultiSelectField",
                        label: this.$t('Status'),
                        name: "Status",
                        value: "",
                        placeholder: this.$t('Enter Status'),
                        validationRules: {
                            "required": false
                        },
                        disabled: false,
                    },
                    {
                        astype: "MultiSelectField",
                        label: this.$t('Type'),
                        name: "Type",
                        value: "",
                        placeholder: this.$t('Enter Type'),
                        validationRules: {
                            "required": false
                        },
                        disabled: false,
                    },
                    {
                        astype: "toggleField",
                        label: this.$t('AdditionalComments'),
                        name: "AdditionalComments",
                        value: "",
                        placeholder: "",
                        config: {
                            onChange: ''
                        }
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "textarea",
                        name: "textarea",
                        value: "",
                        config: {
                            options: []
                        },
                        validationRules: { "required": false },
                        disabled: false,
                    }]
                },
                {
                    layoutType: "double",
                    group_name: 'List of Question/Answering Type(s)',
                    group_display_name: this.$t('ListofQuestionAnsweringType'),
                    Data: [{
                        astype: "TextField",
                        label: this.$t('FeedbackQuestion'),
                        name: "FeedbackQuestion",
                        value: "",
                        placeholder: this.$t('Enter Feedback Question'),
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    }, 
                    {
                        astype: "MultiSelectField",
                        label: this.$t('AnsweringType'),
                        name: "AnsweringType",
                        value: "",
                        placeholder: this.$t('Enter Type'),
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],
                            showAddIcon: true,
                            onAddButtonClick: this.OpenAddTagPopup
                        },
                        disabled: false,
                    }]
                },
                // {
                //     groupId: null,
                //     layoutType: "single",
                //     group_name: this.$t('CompensationBenefitInformation'),
                //     visibility: true,
                //     Data: [
                //         {
                //             astype: "SlotField",
                //             label: '',
                //             name: "CompensationBenefitInformation",
                //             value: "",
                //             visibility: true,
                //             placeholder: "",
                //             config: {
                //                 options: []
                //             },
                //             validationRules: "",
                //         }
                //     ]
                // }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            companyId: null,
            userId: null
        }
    },
    created: function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        if (vm.AddQuestionId != null) {
            vm.FormSchema[0].Data[0].value = vm.AddQuestionId.ticket_feedback_name;
            vm.FormSchema[0].Data[1].value = vm.AddQuestionId.STATUS_ID;
            vm.FormSchema[0].Data[2].value = vm.AddQuestionId.type_code;
            vm.AddQuestion = false;
            vm.EditQuestion = true;
        }
    },
    methods: {
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                userId: vm.userId,
                companyId: vm.companyId,
                departmentname: formfields.Department,
                isvisible: formfields.isvisible == true ? 'true' : 'false',
                description: formfields.Description,
                departmentId: vm.AddQuestionId != null ? vm.AddQuestionId.ticket_feedback_id : 0
            };
            var params = JSON.stringify(jsonobj);
            DataService.SaveDepartment(params).then((response) => {
                if (response.data.Status == "Success") {
                    if (response.data.Code == 0) {
                        vm.ShowAlert(vm.$t('DepartmentAlreadyExist', [vm.$t('AddDepartment')]), "warning", true, vm.$t("Alert"));
                    } else {
                        if (vm.AddQuestionId != null) {
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('AddDepartment')]), "success", true, vm.$t("Alert"));
                        } else {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('AddDepartment')]), "success", true, vm.$t("Alert"));
                        }
                        vm.$parent.FetchData();
                        vm.Close();
                    }
                }
            });

        },
        Close: function () {
            this.$parent.ShowAddQuestion = false;
        },
        // AddRow: function () {
        //     var vm = this;
        //     vm.StockData.push({ stock: "", quantity: "" });
        // },
        // DeleteRow: function (item, index) {
        //     var vm = this;
        //     vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Field")), function () {
        //         vm.StockData.splice(index, 1);
        //     });
        // },
    }
}
</script>
