<template>
<div>
    <loader :is-visible="isLoading"></loader>
    <section class="main-content">
        <div ref="hellp" id="divMaindealData">
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('ManageJobPost') }}</span>
                        <span class="float-right">
                            <a href="javascript:;" class="p-action-btn text-white" :title="$t('BacktoList')" @click="Close">
                                <em class="fa fa-angle-double-left pr-2" aria-hidden="true"></em>
                                <br />{{ $t('BacktoList') }}
                            </a>
                        </span>
                    </div>
                </div>
            </div>
            <div class="border p-3">
                <Form ref="JobPostForm">
                    <dynamic-form ref="ManageJobPostForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        <template v-slot:tgslot-Description="{ data }">
                            <ckeditor :editor="editor" v-model="noteDescription"
                                :class="{ 'is-invalid': data.error != '' }">
                            </ckeditor>
                            <span class="invalid-feedback">{{ data.error }}</span>
                        </template>
                    </dynamic-form>
                </Form>
            </div>
        </div>
        <preview v-if="ShowPreview" :GetPreviewData="GetPreviewData"></preview>
        <ManageDesignation v-if="showManageDesignation"></ManageDesignation>
        <ManageDepartment v-if="showManageDepartment"></ManageDepartment>
        <ManageJobType v-if="showManageJobType"></ManageJobType>
        <ManageSkill v-if="ShowManageSkill"></ManageSkill>
        <ManageSpecialization v-if="ShowManageSpecialization"></ManageSpecialization>
        <ManageIndustryType v-if="ShowManageIndustryType"></ManageIndustryType>
        <ManageFunctionalAreaType v-if="ShowManageFunctionalAreaType" :IsIndustryTypeId="IsIndustryTypeId" :key="reportcomponentkey"></ManageFunctionalAreaType>
        <ManageAddQuestion v-if="ShowManageAddQuestion"></ManageAddQuestion>
    </section>
</div>
</template>

<script>
import preview from './preview.vue';
import ManageDesignation from './ManageDesignation.vue';
import ManageDepartment from './ManageDepartment.vue';
import DataService from '../../../services/DataService';
import ManageJobType from './ManageJobType.vue';
import ManageSkill from './ManageSkill.vue';
import ManageSpecialization from './ManageSpecialization.vue';
import ManageIndustryType from './ManageIndustryType.vue';
import ManageFunctionalAreaType from './ManageFunctionalAreaType.vue';
import moment from 'moment';
import ManageAddQuestion from './ManageAddQuestion.vue';

export default {
    components: {
        preview,
        ManageDesignation,
        ManageDepartment,
        ManageJobType,
        ManageSkill,
        ManageSpecialization,
        ManageIndustryType,
        ManageFunctionalAreaType,
        ManageAddQuestion
    },
    data() {
        return {
            isLoading: false,
            showManageDesignation: false,
            showManageDepartment: false,
            showManageJobType: false,
            ShowPreview: false,
            ShowManageSkill: false,
            ShowManageSpecialization: false,
            ShowManageIndustryType: false,
            ShowManageFunctionalAreaType: false,
            ShowManageAddQuestion: false,
            IsIndustryTypeId: null,
            reportcomponentkey: 0,
            PostId: 0,
            FormSchema: [],
            GetPreviewData:[],
            DesignationName:[],
            editor: this.CLASSIC_EDITOR,
            noteDescription: '',
            LocationName:'',
            LocationDataName:[],
            currencyNameData:'',
            jobTypeName:'',
            skillItemData:[],
            DepartmentName:'',
            industryname:'',
            buttons: [{
                    type: "submit",
                    class: "btn btn-info ",
                    text: "<i class='fa fa-eye pr-2'></i>Preview",
                    isVisible: true,
                    onClick: this.OnSubmit
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                    onClick: this.OnSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ]
        }
    },
    created: async function () {
        this.userId = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;
        this.PostId = this.$route.params.id == undefined ? 0 : this.$route.params.id;
        await this.getForm();
    },
    methods: {
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        ManageAddQuestion() {
            var vm = this;
            vm.ShowManageAddQuestion = true;
        },
        ManageFunctionalAreaType() {
            var vm = this;
            if (vm.IsIndustryTypeId == null) {
                vm.ShowAlert(vm.$t("PleaseSelectIndustryBefore"), "warning", true, vm.$t("Alert"));
            } else {
                vm.reportcomponentkey += 1;
                vm.ShowManageFunctionalAreaType = true;
            }
        },
        ManageIndustryType() {
            var vm = this;
            vm.ShowManageIndustryType = true;
        },
        ManageSpecialization() {
            var vm = this;
            vm.ShowManageSpecialization = true;
        },
        ManageSkill() {
            var vm = this;
            vm.ShowManageSkill = true;
        },
        ManageJobType() {
            var vm = this;
            vm.showManageJobType = true;
        },
        ManageDepartment() {
            var vm = this;
            vm.showManageDepartment = true;
        },
        ManageDesignation() {
            var vm = this;
            vm.showManageDesignation = true;
        },
        formatDate: function (value) {
            if (value) {
                return moment(value, "YYYY-MM-DD").format(String('MM/DD/YYYY'))
            }
        },
        getForm: async function () {
            var vm = this;
            var url = `recordId=${vm.PostId}&moduleName=Hiring&subModuleCode=HR_JOB&langCode=en`;
            await DataService.GetDynamicForm(url).then(function (response) {
                if (response.data != '') {
                    vm.FormSchema = vm.$refs.ManageJobPostForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
                    vm.FormSchema.forEach((group, gi) => {
                        group.Data.forEach(async (field, fi) => {
                            if (field.name == "skill_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageSkill;
                            }
                            if (field.name == "designation_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageDesignation;
                                if(field.value !=''){
                                    field.config.options.forEach(function (itm) {
                                 if(itm.value == field.value){
                                        vm.DesignationName=itm.name;
                                    }
                                });
                                }
                            }
                            if (field.name == "department_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageDepartment;
                                if(field.value !=''){
                                field.config.options.forEach(function (itm) {
                                 if(itm.value == field.value){
                                        vm.DepartmentName=itm.name;
                                    }
                                });
                                }
                            }
                            if (field.name == "job_type_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageJobType;
                                if(field.value !=''){
                                field.config.options.forEach(function (itm) {
                                 if(itm.value == field.value){
                                        vm.jobTypeName=itm.name;
                                    }
                                });
                                }
                            }
                            if (field.name == "crm_lead_industry_type_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageIndustryType;
                                if (field.value != '') {
                                    vm.GetIndustryDropDown('HR_FUN_AREA', field.value);
                                }
                                if(field.value !=''){
                                field.config.options.forEach(function (itm) {
                                 if(itm.value == field.value){
                                        vm.industryname=itm.name;
                                    }
                                });
                                }
                            }
                            if (field.name == "functional_area_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageFunctionalAreaType;
                            }
                            if (field.name == "specilization_stream") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageSpecialization;
                            }
                            if (field.name == "ticket_feedback_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageAddQuestion;
                            }
                            if (field.name == 'company_location_id') {
                                if (response.data.locationData.length > 0) {
                                    var locationvalue = response.data.locationData[0].value;
                                    var locationname = response.data.locationData[0].name;
                                    vm.LocationDataName.push(locationname);
                                    field.value = [{
                                        label: locationname,
                                        value: locationvalue
                                    }];
                                } else {
                                    field.value = field.value;
                                }
                            }
                            if (field.name == 'skill_id') {
                                if (response.data.skillData.length > 0) {
                                    var skillvalue = response.data.skillData[0].value;
                                    var skillname = response.data.skillData[0].name;
                                    vm.skillItemData.push(skillname);
                                    field.value = [{
                                        label: skillname,
                                        value: skillvalue
                                    }];
                                } else {
                                    field.value = field.value;
                                }
                            }
                            if (field.name == "start_date") {
                               field.config = { format: "MM/DD/YYYY", minDate: new Date() };
                            }
                            if (field.name == "close_date") {
                               field.config = { format: "MM/DD/YYYY", minDate: new Date() };
                            }
                            if (field.name == "exp_from") {
                                if (field.value != '') {
                                    vm.GetExpDropDown('HR_EXP_TO', field.value);
                                }
                            }
                            if (field.dtCode == "checkbox" && field.name == "specilization") {
                                vm.FormSchema[1].Data.forEach((field1, fi) => {
                                    if (field.value == '1') {
                                        if (field1.name == "specilization_stream" && field.value == '1') {
                                            field1.visibility = true;
                                        }
                                    } else {
                                        if (field1.name == "specilization_stream") {
                                            field1.visibility = false;
                                        }
                                    }
                                });
                            }
                            if (field.name  == 'Description') {
                                vm.noteDescription = field.value;
                            }
                            field.config.onChange = vm.onChangeEventForFields;
                        })
                    });
                }
            });
        },
        onChangeEventForFields: function (x, y, z) {
            var vm = this;
            if (y.name == "crm_lead_industry_type_id") {
                vm.IsIndustryTypeId = y.value;
                vm.reportcomponentkey += 1;
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (field.name == 'functional_area_id') {
                            let id = y.value;
                            await vm.GetDropDownData('HR_FUN_AREA', field, id, null);
                        }
                    })
                });
                vm.industryname='';
                y.config.OptionsForBind.forEach(function (itm) {
                    if(itm.value == y.value){
                          vm.industryname=itm.name;
                    }
                });
            }
            if (y.name == "start_date") {               
                this.$delete(y, 'validationRules');              
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        if (field.name == "close_date") {
                            field.value = "";
                            field.config.minDate = x;
                            this.$delete(field, 'validationRules'); 
                        }
                    });
                });               
            }        
            if (y.name == "close_date") {
                this.$delete(y, 'validationRules');   
            }    
            if (y.name == "exp_from") {
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (field.name == 'exp_to') {
                            let id = y.value;
                            await vm.GetDropDownData('HR_EXP_TO', field, id, null);
                        }
                    })
                })
            }
            if (y.name == "specilization") {
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (y.value[0] == '1' || y.value[1] == '1') {
                            if (field.name == 'specilization_stream') {
                                field.visibility = true;
                            }
                        } else {
                            if (field.name == 'specilization_stream') {
                                field.visibility = false;
                            }
                        }

                    })
                })
            }
            if(y.name=='designation_id'){
                vm.DesignationName='';
                y.config.OptionsForBind.forEach(function (itm) {
                    if(itm.value == y.value){
                          vm.DesignationName=itm.name;
                    }
                });
            }
            vm.LocationName='';
            if(y.name=='company_location_id'){
               vm.LocationName=z.label;
               vm.LocationDataName.push(z.label);
            }
            if(y.name=='currency'){
                y.config.OptionsForBind.forEach(function (itm) {
                    if(itm.value == y.value){
                          vm.currencyNameData=itm.name;
                    }
                });
            }
            if(y.name=='job_type_id'){
                vm.jobTypeName='';
                y.config.OptionsForBind.forEach(function (itm) {
                    if(itm.value == y.value){
                          vm.jobTypeName=itm.name;
                    }
                });
            }
            var skillName='';
            if(y.name=='skill_id'){
                skillName=z.label;
               vm.skillItemData.push(skillName);
            }
            if(y.name=='department_id'){
                vm.DepartmentName='';
                y.config.OptionsForBind.forEach(function (itm) {
                    if(itm.value == y.value){
                          vm.DepartmentName=itm.name;
                    }
                });
            }
        },
        GetDropDownData: async function (fieldCode, field, id, ths) {
            var ths = this;
            if (field.name == 'functional_area_id') {
                var url = `id=&moduleName=HIRING&type=${fieldCode}&search=&refId=${id}`;
                await DataService.GetDDLData(url).then(function (response) {
                    if (response.data != '') {
                        field.config.options = [];
                        var json = response.data;
                        if (json != null) {
                            field.config.options = response.data.DATA;
                            ths.$refs.ManageJobPostForm.UpdateKeyValue();
                        }
                    } else {
                        field.config.options = [];
                    }
                });
            }
            if (field.name == 'exp_to') {
                var url = `id=&moduleName=HIRING&type=${fieldCode}&search=&refId=${id}`;
                await DataService.GetDDLData(url).then(function (response) {
                    if (response.data != '') {
                        field.config.options = [];
                        var json = response.data;
                        if (json != null) {
                            field.config.options = response.data.DATA;
                            ths.$refs.ManageJobPostForm.UpdateKeyValue();
                        }
                    } else {
                        field.config.options = [];
                    }
                });
            }
        },
        GetIndustryDropDown: async function (fieldCode, id) {
            var ths = this;
            var url = `id=&moduleName=HIRING&type=${fieldCode}&search=&refId=${id}`;
            await DataService.GetDDLData(url).then(function (response) {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (field.name == 'functional_area_id') {
                            field.value = field.value;
                            field.config.options = [];
                            if (response.data.DATA.length > 0) {
                                field.config.options = response.data.DATA;
                            }
                            ths.$refs.ManageJobPostForm.UpdateKeyValue();
                        }
                    })
                })
            });
        },
        GetExpDropDown: async function (fieldCode, id) {
            var ths = this;
            var url = `id=&moduleName=HIRING&type=${fieldCode}&search=&refId=${id}`;
            await DataService.GetDDLData(url).then(function (response) {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (field.name == 'exp_to') {
                            field.value = field.value;
                            field.config.options = [];
                            if (response.data.DATA.length > 0) {
                                field.config.options = response.data.DATA;
                            }
                            ths.$refs.ManageJobPostForm.UpdateKeyValue();
                        }
                    })
                })
            });
        },
        Close: function () {
            this.$router.push({ name: 'JobList' });
        },
        onSubmit(formfields, val) {
            var vm = this;
            var inputString = val;
            var previewRegex = /<\/i>(.*)/;
            var previewMatch = inputString.match(previewRegex);
            var RequestFrom = previewMatch ? previewMatch[1] : "";
            
            
            vm.$refs.JobPostForm.validate().then(result => {
                if (result) {
                    var CustomFieldArray = vm.$refs.ManageJobPostForm.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchema);
                    CustomFieldArray.forEach((x) => {
                        if (x.field_name == 'skill_id') {
                            if (!vm.isBlank(x.field_value)) {
                                x.field_value = x.field_value[0].value;
                            }
                        }
                        if (x.field_name == 'company_location_id') {
                            if (!vm.isBlank(x.field_value)) {
                                x.field_value = x.field_value[0].value;
                            }
                        }
                        if (vm.PostId > 0) {
                            if (x.field_name == 'modified_by') {
                                x.field_value = vm.userId;
                            }
                        }
                        if (x.field_name == 'post_id') {
                            x.field_value = vm.PostId;
                        }
                        if (x.field_name == 'company_id') {
                            x.field_value = vm.companyId;
                        }
                        if (x.field_name == 'status_id') {
                            x.field_value = x.field_value;
                        }
                        if (x.field_name == 'open_to') {
                            x.field_value = 'E';
                        }
                        if (x.field_name == 'created_by') {
                            x.field_value = vm.userId;
                        }
                        if (x.field_name == 'created_at') {
                            x.field_value = vm.$options.filters.formatDate(new Date());
                        }
                        if (x.field_name == 'modified_at') {
                            x.field_value = vm.$options.filters.formatDate(new Date());
                        }
                        if (x.field_name == "specilization" || x.field_name == "is_visible_contact" || x.field_name == "visible_to_candidate") {
                            if (x.field_value > '0') {
                                x.field_value = '1';

                            } else {
                                x.field_value = '0';
                            }
                        }
                        if (x.field_name == 'Description') {
                            x.field_value = vm.noteDescription;
                        }
                    })
                    var postJSON = {
                        data: CustomFieldArray,
                    }
                    var objectPostString = JSON.stringify(postJSON);
                    if(RequestFrom=='Preview'){
                    let data=postJSON.data;
                    var PreviewJSON = {
                    skillData : vm.skillItemData,
                    location :vm.LocationDataName,
                    postTitle :data[0].field_value,
                    jobTitle : data[1].field_value,
                    position :data[3].field_value,
                    experience_from: data[8].field_value,
                    experience_to : data[9].field_value,
                    currency :vm.currencyNameData,
                    salaryRangeFrom :data[11].field_value,
                    salaryRangeTo:data[12].field_value,
                    startDate : data[5].field_value,
                    closeDate :data[6].field_value,
                    jobType : vm.jobTypeName,
                    designation : vm.DesignationName,
                    department : vm.DepartmentName,
                    industry : vm.industryname,
                    contactPerson :data[34].field_value,
                    phoneNumber: data[35].field_value,
                    companyWebsite :data[36].field_value,
                    companyName :data[37].field_value,
                    boutCompany:data[38].field_value,
                    shortdescription : data[17].field_value,
                    description:data[18].field_value
                    }
                    vm.GetPreviewData=PreviewJSON;
                    vm.ShowPreview = true;
                    }else{
                    DataService.SaveHRJob(objectPostString).then((response) => {
                        if (response.data.Status == 'Success') {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("JobPost")]), "success", true, vm.$t("Alert"));
                            if (vm.PostId > 0) {
                                vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("JobPost")]), "success", true, vm.$t("Alert"));
                            }
                            vm.Close();
                        } else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("JobPost")]), "failure", true, vm.$t("Alert"));
                        }
                    })
                }
                }
            })
        }
    }
}
</script>
